import React, { useEffect,useState, useRef } from 'react';
import Chart from 'chart.js/auto';
import { connect } from 'react-redux';
import { Navigate, Link } from 'react-router-dom';
import axios from 'axios';
import './css/Home.css';
import Footer from '../components/Footer';
import Navbar from '../components/Navbar';

const Home = () => {

    const chartRef = useRef(null);
    const chartInstance = useRef(null); // Store the chart instance

    // Sample data for demonstration purposes (expenditure for the current week)
    const weeklyExpenditureData = [50, 60, 40, 70, 55, 80, 65]; // Weekly expenditure data
    const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  const user = localStorage.getItem('user_id');

  const config = {
    headers: {
      Authorization: `JWT ${localStorage.getItem('access')}`,
    },
  };

  const budget = 1000; // Replace with the user's budget
  const amountUsed = 750; // Replace with the actual amount used
  const monthlyExpenditureData = [300, 450, 600, 800, 750, 900, 700, 650, 850, 950, 750, 700]; // Monthly expenditure data
  const topServicesUsed = [
    { name: 'Netflix', amount: 120 },
    { name: 'Spotify', amount: 60 },
    { name: 'Amazon Prime', amount: 90 },
    { name: 'Gym Membership', amount: 80 },
    { name: 'Uber', amount: 70 },
  ];

  const budgetPercentage = (amountUsed / budget) * 100;

  useEffect(() => {
    // Destroy the previous chart instance if it exists
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }

    // Create a new chart
    const ctx = chartRef.current.getContext('2d');
    chartInstance.current = new Chart(ctx, {
      type: 'line', // Use a line chart
      data: {
        labels: daysOfWeek,
        datasets: [{
          label: 'Weekly Expenditure',
          data: weeklyExpenditureData,
          borderColor: 'blue', // Set the line color to blue
          borderWidth: 2, // Set the line width
          fill: false, // Disable fill under the line
        }],
      },
      options: {
        responsive: true,
        scales: {
          y: {
            beginAtZero: true,
          },
        },
      },
    });
  }, []);
  

  return (
    <div className="homepage">
      <div className="container">
        <div className="row">
          <div className="col-md-4">
            <div className="budget-info">
              <h2>Budget Overview</h2>
              <p className="budget-amount">Amount Used: ${amountUsed}</p>
              <p className="budget-amount">Budget Set: ${budget}</p>
              <div className="progress">
                <div
                  className="progress-bar"
                  role="progressbar"
                  style={{ width: `${budgetPercentage}%` }}
                  aria-valuenow={budgetPercentage}
                  aria-valuemin="0"
                  aria-valuemax="100"
                >
                  {budgetPercentage.toFixed(2)}%
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-8">
            <div className="chart">
              {/* Insert your chart component here */}
              <p>Weekly Expenditure Chart</p>
              <canvas ref={chartRef} id="weeklyExpenditureChart" width="400" height="200"></canvas>
              
            </div>
          </div>
        </div>
        <div className="row mt-5" >
          <div className="col-md-12" id='table'>
            <h2>Top 5 Services Used</h2>
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>Service</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody>
                {topServicesUsed.map((service, index) => (
                  <tr key={index}>
                    <td>{service.name}</td>
                    <td>${service.amount}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
  );
};



export default Home;
