import React from 'react';
import { NavLink } from 'react-router-dom';
import './css/navbar.css'

const Footer = () => {
    return (
        <div>
        <nav className="bottom-navbar">
          <ul className="navbar-list">
            <li className="navbar-item">
              <NavLink to="/analysis" activeClassName="active-link"> <i class="fa fa-bar-chart" aria-hidden="true"></i></NavLink>
            </li>
            <li className="navbar-item">
              <NavLink to="/add" activeClassName="active-link"> <i className="fa fa-plus" aria-hidden="true"></i> </NavLink>
            </li>
            <li className="navbar-item">
              <NavLink to="/account" activeClassName="active-link"> <i class="fa fa-user" aria-hidden="true"></i> </NavLink>
            </li>
          </ul>
        </nav>
        <div class="container" style={{textAlign:"center", fontWeight:'lighter', color:"GrayText"}}>
            <p>&copy; 2023 Expensehub. All rights reserved.</p>
        </div>
        </div>
    );
}

export default Footer;
