import React, { useState } from 'react';
import Footer from '../components/Footer';

const ExpensePage = () => {
  const [formData, setFormData] = useState({
    name: '',
    category: '', // Initialize category as an empty string
    amount: '',
    date: '',
  });

  const [customCategory, setCustomCategory] = useState(''); // State for custom category

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === 'category' && value === 'custom') {
      // If the user selects "Custom Category," clear the customCategory input
      setCustomCategory('');
    }
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate the form data here (e.g., check if amount is a valid number)

    // Use the selected category or custom category from the input
    const category = formData.category === 'custom' ? customCategory : formData.category;

    // Create an object with the correct category value
    const expenditureData = {
      ...formData,
      category,
    };

    try {
      const response = await fetch('YOUR_API_ENDPOINT', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(expenditureData),
      });

      if (response.ok) {
        // Expenditure data successfully posted to the API
        // You can handle success (e.g., reset the form, update the UI)
        setFormData({
          name: '',
          category: '', // Reset category to an empty string
          amount: '',
          date: '',
        });
        setCustomCategory(''); // Reset custom category input
        console.log('Expenditure data posted successfully');
      } else {
        // Handle API error (e.g., show an error message)
        console.error('Failed to post expenditure data to the API');
      }
    } catch (error) {
      // Handle network or other errors
      console.error('An error occurred:', error);
    }
  };

  // Define a list of common categories
  const commonCategories = [
    'Entertainment',
    'Food',
    'Transportation',
    'Shopping',
    'Health',
    // Add more common categories as needed
  ];

  return (
    <div className="expense-page">
      <h3 style={{textAlign:'center'}}>Log an Expenditure</h3>
      <form onSubmit={handleSubmit} style={{textAlign:"center", width:'80%',borderRadius:'10px', boxShadow:'5px 10px 18px #888888', margin:'auto', padding:"5%"}}>
        <div className="mb-3">
          <label htmlFor="name" className="form-label">Name</label>
          <input
            type="text"
            className="form-control"
            name="name"
            id="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-3">
          <label htmlFor="category" className="form-label">Category</label>
          <select
            className="form-select"
            name="category"
            id="category"
            value={formData.category}
            onChange={handleChange}
            required
          >
            <option value="" disabled>Select a category</option>
            {commonCategories.map((category) => (
              <option key={category} value={category}>
                {category}
              </option>
            ))}
            <option value="custom">Custom Category</option> {/* Allow typing a custom category */}
          </select>
          {/* Render an input field for custom category if selected */}
          {formData.category === 'custom' && (
            <input
              type="text"
              className="form-control mt-2"
              name="customCategory"
              placeholder="Enter custom category"
              value={customCategory}
              onChange={(e) => setCustomCategory(e.target.value)}
              required
            />
          )}
        </div>
        <div className="mb-3">
          <label htmlFor="amount" className="form-label">Amount</label>
          <input
            type="number"
            className="form-control"
            name="amount"
            id="amount"
            value={formData.amount}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-3">
          <label htmlFor="date" className="form-label">Date</label>
          <input
            type="date"
            className="form-control"
            name="date"
            id="date"
            value={formData.date}
            onChange={handleChange}
            required
          />
        </div>
        <button type="submit" className="btn btn-primary">Add Expenditure</button>
      </form>
      <Footer/>
    </div>
  );
};

export default ExpensePage;
