import React from 'react';
import { Pie } from 'react-chartjs-2'; // Import Chart.js for React
import { useState } from 'react';
import Footer from '../components/Footer';

const AnalysisPage = () => {
  // Sample data for demonstration purposes
  const servicesData = [
    { name: 'Netflix', category: 'Entertainment', expense: 50 },
    { name: 'Spotify', category: 'Entertainment', expense: 30 },
    { name: 'Amazon Prime', category: 'Shopping', expense: 60 },
    { name: 'Gym Membership', category: 'Health', expense: 80 },
    { name: 'Uber', category: 'Transportation', expense: 70 },
    { name: 'Groceries', category: 'Food', expense: 200 },
    { name: 'Electricity Bill', category: 'Utilities', expense: 60 },
    { name: 'Internet', category: 'Utilities', expense: 50 },
    { name: 'Gasoline', category: 'Transportation', expense: 40 },
    { name: 'Restaurant Dining', category: 'Food', expense: 90 },
    { name: 'Clothing', category: 'Shopping', expense: 120 },
    { name: 'Phone Bill', category: 'Utilities', expense: 40 },
    { name: 'Health Insurance', category: 'Health', expense: 100 },
    { name: 'Travel', category: 'Transportation', expense: 150 },
    { name: 'Movie Tickets', category: 'Entertainment', expense: 25 },
    { name: 'Books', category: 'Education', expense: 35 },
    { name: 'Coffee Shop', category: 'Food', expense: 20 },
    { name: 'Home Rent', category: 'Housing', expense: 600 },
    { name: 'Car Insurance', category: 'Transportation', expense: 80 },
    { name: 'Haircut', category: 'Personal Care', expense: 15 },
  ];

  // Calculate the total expenditure
  const totalExpenditure = servicesData.reduce((total, service) => total + service.expense, 0);
  const [visibleServices, setVisibleServices] = useState(5); // Initially, display 5 services


  // Prepare data for the pie chart
  const categories = [...new Set(servicesData.map(service => service.category))];
  const categoryExpenses = categories.map(category => {
    const categoryServices = servicesData.filter(service => service.category === category);
    const categoryExpense = categoryServices.reduce((total, service) => total + service.expense, 0);
    return categoryExpense;
  });

  // Define colors for the pie chart
  const colors = ['#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', '#9966FF', '#FF9F40', '#FF6699', '#6699FF', '#FF99CC', '#99CCFF'];

    // Function to handle "View More" button click
    const handleViewMoreClick = () => {
        // Increase the number of visible services (e.g., by 5)
        setVisibleServices(prevVisibleServices => prevVisibleServices + 5);
        };
    

  // Prepare data for the pie chart
  const pieChartData = {
    labels: categories,
    datasets: [
      {
        data: categoryExpenses,
        backgroundColor: colors,
      },
    ],
  };

    // Define chart options to adjust the chart size
  const chartOptions = {
    responsive: true,
  };


  return (
    <div className="analysis-page">


      {/* List of Services */}
      <div className="services-list" style={{width:'80%', margin:'auto', textAlign:'center', borderRadius:'10px', boxShadow:'5px 10px 18px #888888'}}>
        <h3>Services and Categories</h3>
        <hr/>
        <ul style={{listStyle:'none'}}>
          {servicesData.slice(0, visibleServices).map((service, index) => (
            <li key={index}>
              {service.name} - {service.category}
            </li>
          ))}
        </ul>
        {visibleServices < servicesData.length && (
          <button onClick={handleViewMoreClick} className="btn btn-outline-info" style={{marginBottom:'2.5em'}}>View More</button>
        )}
      </div>

      {/* Pie Chart */}
      <div className="pie-chart" style={{width:'80%', margin:'auto', textAlign:'center', boxShadow:'5px 10px 18px #888888', borderRadius:'10px', marginTop:'2.5em'}}>
            <h3>Contribution to Total Expenditure</h3>
            <hr/>
        <div style={{height:'20%',width:'60%', margin:'auto'}}>
     
             <Pie data={pieChartData} options={chartOptions} />
        </div>
        
      </div>

      {/* Download Report Button */}
      <div className="download-button" style={{textAlign:'center', marginTop:'3em'}}>
        <button className="btn btn-success">Download Report</button>
      </div>
      <Footer/>
    </div>
  );
};

export default AnalysisPage;
