import React from 'react'
import './index.css'
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from './containers_core/Home';
import { Signup, Login, ResetPassword, ResetPasswordConfirm, Activate} from './containers_auth';
import { Provider } from 'react-redux';
import store from './store';
import Layout from './hocs/Layout';
import About from './containers_core/About';
import AnalysisPage from './containers_core/Analysis';
import ExpensePage from './containers_core/Add';
import UserAccountPage from './containers_core/Account';



const App = () => 
    (
    <Provider store={store}>
        <BrowserRouter>
                <Routes>
                    <Route path="/" exact element={<About/>} />
                    <Route path="/home" exact element={<Layout>< Home /></Layout>} />
                    <Route path="/analysis" exact element={<Layout>< AnalysisPage /></Layout>} />
                    <Route path="/add" exact element={<Layout><ExpensePage/></Layout>} />
                    <Route path="/account" exact element={<Layout><UserAccountPage/></Layout>} />
                    <Route exact path='/login' element={<Login/>} />
                    <Route exact path='/signup' element={<Signup/>} />
                    <Route exact path='/reset-password' element={<ResetPassword/>} />
                    <Route exact path='/password/reset/confirm/:uid/:token' element={<ResetPasswordConfirm/>} />
                    <Route exact path='/activate/:uid/:token' element={<Activate/>} />
                </Routes>
        </BrowserRouter>
    </Provider>
  )

export default App