import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';


const Navbar = () => {
  const guestLinks = (
    <Fragment>
      <li className='nav-item'>
        <Link className='nav-link' to='/sooner'>
          <button type="button" className="btn btn-light">Sign up</button>
        </Link>
      </li>
      <li className='nav-item'>
        <Link className='nav-link' to='/sooner'>
          <button type="button" className="btn btn-outline-danger">Login</button>
        </Link>
      </li>

    </Fragment>
  );



  return (
    <nav className='navbar navbar-expand-lg navbar-light bg-light fixed-top'>
      <div className='container'>
        <Link className='navbar-brand' to='/' style={{fontWeight:'bold'}}> Expensehub</Link>
        <button className='navbar-toggler' type='button' data-bs-toggle='collapse' data-bs-target='#navbarNav'
          aria-controls='navbarNav' aria-expanded='false' aria-label='Toggle navigation'>
          <span className='navbar-toggler-icon'></span>
        </button>
        <div className='collapse navbar-collapse justify-content-end' id='navbarNav'>
          <ul className='navbar-nav'>
            {guestLinks}
          </ul>
        </div>
      </div>
    </nav>
  );
};



export default Navbar;
