import React, { useState } from 'react';
import Footer from '../components/Footer';

const UserAccountPage = () => {
  // Sample user data (replace with actual user data from your authentication system)
  const [userData, setUserData] = useState({
    username: 'exampleuser',
    email: 'user@example.com',
    fullName: 'John Doe',
  });

  // State for editing user information
  const [isEditing, setIsEditing] = useState(false);
  const [editedUserData, setEditedUserData] = useState({ ...userData });

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleCancelEdit = () => {
    setIsEditing(false);
    setEditedUserData({ ...userData });
  };

  const handleSaveEdit = () => {
    // Perform API call to update user information with `editedUserData`
    // Replace this with actual API call logic
    // After successful update, you can update `userData` and exit editing mode

    // For demonstration purposes, we'll simply update the local state
    setUserData({ ...editedUserData });
    setIsEditing(false);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedUserData({
      ...editedUserData,
      [name]: value,
    });
  };

  return (
    <div className="container mt-5">
      <h3 className="mb-4">User Account</h3>
      <div className="card">
        <div className="card-body">
          <h4 className="card-title">User Information</h4>
          {isEditing ? (
            <div>
              <div className="mb-3">
                <label htmlFor="username" className="form-label">Username</label>
                <input
                  type="text"
                  className="form-control"
                  name="username"
                  id="username"
                  value={editedUserData.username}
                  onChange={handleChange}
                  required
                />
              </div>
              <div className="mb-3">
                <label htmlFor="email" className="form-label">Email</label>
                <input
                  type="email"
                  className="form-control"
                  name="email"
                  id="email"
                  value={editedUserData.email}
                  onChange={handleChange}
                  required
                />
              </div>
              <div className="mb-3">
                <label htmlFor="fullName" className="form-label">Full Name</label>
                <input
                  type="text"
                  className="form-control"
                  name="fullName"
                  id="fullName"
                  value={editedUserData.fullName}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          ) : (
            <div>
              <p><strong>Username:</strong> {userData.username}</p>
              <p><strong>Email:</strong> {userData.email}</p>
              <p><strong>Full Name:</strong> {userData.fullName}</p>
            </div>
          )}
          {isEditing ? (
            <div>
              <button className="btn btn-primary" onClick={handleSaveEdit}>Save</button>
              <button className="btn btn-secondary ms-2" onClick={handleCancelEdit}>Cancel</button>
            </div>
          ) : (
            <button className="btn btn-primary" onClick={handleEditClick}>Edit Profile</button>
          )}
        </div>
      </div>
      {/* Add other sections for password change, account settings, etc. */}

        <Footer/>
  
    </div>
  );
};

export default UserAccountPage;
